import flatpickr from 'flatpickr';
import { Korean } from 'flatpickr/dist/l10n/ko';
import { pages, datepicker } from '../elements';

if (pages.order !== null) {
    const date = datepicker.triger;

    flatpickr(date, {
        locale: Korean,
        enableTime: true,
        dateFormat: 'Y-m-d H:i',
        disableMobile: 'true',
        minDate: 'today',
        wrap: true
    });
}
